<template>
	<div class="personal-content-body">
		<div class="content">
			<!-- <div class="content-title clear">
				<div class="left">行政管理</div>
			</div> -->
			<div class="content-body">
				<div class="createapply-home-icongroup" v-for="item in applyTypelist" v-if="item.children.length!=0">
					<div class="createapply-home-icongroup-title">
						{{item.title}}
					</div>
					<div class="createapply-home-icon-itemlist">
						<div class="createapply-home-iconitem" @click="gotoCreateApply(item1,item.type)" v-for="item1 in item.children">
							<img :src="item1.url" class="">
							<div>{{item1.name}}</div>
							<span v-if="item1.num>0"></span>
						</div>
					</div>
				</div>
				
				<div class="createapply-home-icongroup" v-if="permissionsManageList.child.length!=0">
					<div class="createapply-home-icongroup-title">
						{{permissionsManageList.name}}
					</div>
					<div class="createapply-home-icon-itemlist">
						<div class="createapply-home-iconitem" @click="goPermissionsMoudle(item1)" v-for="item1 in permissionsManageList.child">
							<img :src="'../static/images/adminhomepage_'+item1.name+'.png'" class="">
							<div>{{item1.node_name}}</div>
						</div>
					</div>
				</div>
				
			</div>
		</div>
	</div>
</template>
<script>
	//审批流程状态
	import {
		dialogConfirm,
		compareJson,
		dialogConfirmHasbtn,
		getStore
	} from '../../config/mUtils'
	import {
		getApplyTypeNew,
		getUserMenu
	} from '../../config/api'
	export default {
		data() {
			return {
				applyTypelist: [],
				permissionsManageList: {
					name: '审批管理',
					isPermissions: true,
					child: []
				}
			}
		},
		created() {
			//获取审批类型
			getApplyTypeNew({}).then(res => {
				if(res.data.msgNo == 1) {
					let tl = res.data.payload;
					this.applyTypelist = res.data.payload;
					console.log(res);
				}
			})
			//获取用户权限
			getUserMenu({
				user_id: getStore('htcx_uid'),
//				specMenuId: 20
				status: 3,
				specMenuId: 34
			}).then(res => {
				if(res.data.msgNo == 1) {
					this.permissionsManageList.child = res.data.payload;
					let newObj = {
						hidden: 0,
						id: 34,
						is_menu: 2,
						name: "applyListNewFirst",
						node_name: "审批列表",
						pid: 20,
						seq: 9,
						status: 1,
						style: "",
					}
//					this.permissionsManageList.child.push(newObj);
				} else {
					this.permissionsManageList.child = []
				}
			})
		},
		components: {},
		computed: {},
		mounted() {},
		methods: {
			beforeRouteEnter(to, from, next) {
				next((vm) => {
					vm.realList = to.meta.routeList;
				});
			},
			gotoCreateApply(item, type) {
				this.$router.push({
					name: 'applyListNew',
					query: {
						template_id: item.id,
						aType: type,
						aName: item.name,
						from: 3
					}
				})

			},
			getImgurl(e) {
				e.currentTarget.src = '../../static/images/header_userimg.png';
				e.onerror = null;
			},
			goPermissionsMoudle(item) {
				if(item.name == 'applyListNewFirst') {
					this.$router.push({
						name: item.name,
					})
				} else {
					this.$router.push({
						name: item.name,
						query: {
							from: 3
						}
					})
				}

			}
		}
	}
</script>
<style lang="scss" scoped>
	@import '../../style/apply';
	.content{
		min-height: 750px;
		border-radius: 15px;
		// overflow-y: scroll;
		box-sizing: border-box;
	}
	.personal-content-body {
		.content-title{
			.left{
				font-weight: 400;
			}
		}
		.content-body {
			// position: absolute;
			// padding: 18px 16px 16px;
			// left: 36px;
			// right: 36px;
			// top: 80px;
			// bottom: 18px;
		}
	}


	// .personal-content .content-body {
	// 	position: absolute;
	// 	// border: 1px solid #E4E4E4;
	// 	padding: 18px 16px 16px;
	// 	left: 36px;
	// 	right: 36px;
	// 	top: 80px;
	// 	bottom: 18px;
	// 	// overflow: auto;
	// }
</style>


